/*СТИЛИ ДЛЯ КАСТОМНОГО СЕЛЕКТА НАЧАЛО*/
.custom-select-wrapper {
  position: relative;
  display: block;
  user-select: none;
  background: #f4f5f4;
  margin-bottom: 1.38889vw; }
  @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
    .custom-select-wrapper {
      margin-bottom: 5.55556vmin; } }
  .custom-select-wrapper select {
    display: none; }

.custom-select {
  position: relative;
  display: block;
  font-size: 1.11111vw;
  padding-left: 1.04167vw; }
  @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
    .custom-select {
      padding-left: 4.16667vmin;
      font-size: 4.44444vmin; } }

.custom-select-trigger {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 2.98611vw;
  width: 100%;
  cursor: pointer; }
  @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
    .custom-select-trigger {
      height: 11.94444vmin; } }
  .custom-select-trigger:after {
    position: absolute;
    display: block;
    content: '';
    width: 0.69444vw;
    height: 0.69444vw;
    top: 50%;
    right: 1.73611vw;
    margin-top: -0.20833vw;
    border-bottom: 1px solid #373F41;
    border-right: 1px solid #373F41;
    transform: rotate(45deg) translateY(-50%);
    transition: all .4s ease-in-out;
    transform-origin: 50% 0; }
    @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
      .custom-select-trigger:after {
        width: 2.77778vmin;
        height: 2.77778vmin;
        right: 3.33333vmin; } }

.custom-select.opened .custom-select-trigger:after {
  margin-top: 3px;
  transform: rotate(-135deg) translateY(-50%); }

.custom-options {
  position: absolute;
  display: block;
  top: 100%;
  left: 0;
  right: 0;
  min-width: 100%;
  border-radius: 4px;
  box-sizing: border-box;
  background: #fff;
  transition: all .4s ease-in-out;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-15px);
  box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.07); }

.custom-select.opened .custom-options {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
  z-index: 10;
  transform: translateY(0); }

.custom-option {
  position: relative;
  padding-left: 1.04167vw;
  font-size: 1.11111vw;
  height: 2.98611vw;
  cursor: pointer;
  display: flex;
  align-items: center;
  background: #f4f5f4; }
  .custom-option::after {
    content: '';
    display: inline-block;
    position: absolute;
    border: 1px solid #373F41;
    border-radius: 50%;
    right: 0;
    opacity: 0; }
  .custom-option::before {
    content: '';
    display: inline-block;
    position: absolute;
    background: #373F41;
    border-radius: 50%;
    right: 0;
    opacity: 0; }
  @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
    .custom-option {
      padding-left: 4.16667vmin;
      font-size: 4.44444vmin;
      height: 11.94444vmin; } }
  @media (max-width: 500px), (max-height: 500px) and (orientation: landscape) {
    .custom-option.selection::after, .custom-option.selection::before {
      opacity: 1; }
    .custom-option.selection::after {
      width: 4.44444vmin;
      height: 4.44444vmin;
      right: 3.33333vmin; }
    .custom-option.selection::before {
      width: 2.22222vmin;
      height: 2.22222vmin;
      right: 4.72222vmin; } }
  @media (hover: hover) {
    .custom-option:hover {
      background: linear-gradient(130.16deg, #82BB58 2.89%, #53A580 97.29%);
      color: #f4f5f4; } }
  .custom-option:last-of-type {
    border-bottom: 0; }

