.popupWrapper {
    position: fixed;
    display: none;
    justify-content: center;
    align-items: center;
    background: #e5e5e5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 200;
    box-sizing: border-box;
    @include mobile {
        align-items: flex-start;
        background: linear-gradient(130.16deg, #82BB58 2.89%, #53A580 97.29%);
        overflow-x: hidden;
        overflow-y: scroll;
        height: 100%;
        min-height: 100vh; }
    >* {
        box-sizing: border-box; }
    &.product-page {
        display: flex;
        position: unset; }
    .chooseSong,.congratulations {
        display: none; }
    &[data-step="1"] {
        .chooseSong {
            display: block; } }
    &[data-step="2"] {
        .congratulations {
            display: block; } }
    .modal {
        background: $lightTextOne;
        width: get-vw(1100px);
        position: relative;
        margin: auto;
        box-shadow: 0px get-vw(4px) get-vw(15px) rgba(0, 0, 0, 0.07);
        @include mobile {
            width: 100%;
            margin: 0;
            background: none;
            box-shadow: none; }
        &__title {
