                color: $titleText;
                @include mobile {
                    background: none;
                    font-size: get-vw-mobile(26px);
                    line-height: get-vw-mobile(33px);
                    text-align: center;
                    color: $white;
                    margin-bottom: get-vw-mobile(20px); } }
            &_button {
                position: absolute;
                width: get-vw(12px);
                height: get-vw(12px);
                display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                right: get-vw(28px);
                cursor: pointer;
                @include mobile {
                    display: none; }
                &::after, &::before {
                    content: '';
                    display: inline-block;
                    width: get-vw(15px);
                    height: get-vw(2px);
                    background: $titleText;
                    position: absolute;
                    transform: rotate(45deg); }
                &::before {
                    transform: rotate(-45deg); } } }
