            font-size: get-vw(16px);
            border: 1px solid #0D1018;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: get-vw(40px);
            cursor: pointer;
            @include mobile {
                margin: 0 get-vw-mobile(20px);
                height: get-vw-mobile(52px);
                border-radius: get-vw-mobile(40px);
                font-size: get-vw-mobile(16px);
                font-weight: 600;
                padding: 0; }
            svg {
                width: get-vw(17px);
                margin: 0 get-vw(10px);
                @include mobile {
                    margin: 0 get-vw-mobile(10px); } }
            &.cartBtnPrev {
                padding-left: get-vw(22px);
                svg {
                    width: get-vw(16px);
                    @include mobile {
                        width: get-vw-mobile(14px); } } }
            &.cartBtnNext {
                padding-right: get-vw(22px);
                @include mobile {
                    margin-top: get-vw-mobile(10px); }
                svg {
                    @include mobile {
                        width: get-vw-mobile(17px); }
                    path {
                        fill: #0D1018; } } }
            &:last-child {
                svg {
                    transform: rotate(180deg); } }
            &:hover {
                background: #0D1018;
                color: $lightTextTwo;
                svg {
                    path {
                        fill: $lightTextTwo; } } } } }
